import {
  IonContent,
  IonPage,
  IonText,
  IonInput,
  IonButton,
  IonCheckbox,
  IonHeader,
  IonToolbar,
  IonTitle,
  IonTextarea,
  IonFooter,
} from "@ionic/react";
import React from "react";
import { FieldValues, useForm } from "react-hook-form";
import Input, { InputProps } from "../components/Input";
import * as yup from "yup";
import { yupResolver } from '@hookform/resolvers/yup';
import CommonHeader from '../components/CommonHeader';
import CommonFooter from '../components/CommonFooter';

const Tab1: React.FC = () => {
  const validationSchema = yup.object().shape({
    matrcula: yup.string().required("informacin requerida").min(2).max(32),
    nombreDoc: yup.string().required("informacin requerida").min(2).max(32),
    apellidoDoc: yup.string().required("informacin requerida").min(2).max(32),
    contrasea: yup.string().required("informacin requerida").min(4, "la contrasea debe contener por lo menos 4 caracteres"),
    trminos: yup.boolean().required("debe aceptar los trminos y condiciones").oneOf([true], "debe aceptar los trminos y condiciones"),
  });
  let initialValues = {
    matrcula: undefined,
    nombreDoc: undefined,
    apellidoDoc: undefined,
    contrasea: undefined,
    terminos: undefined,
  };
  const { control, handleSubmit, reset, formState: { errors } } = useForm<FieldValues>({
    defaultValues: { ...initialValues },
    resolver: yupResolver(validationSchema),
    mode: 'onSubmit',
    reValidateMode: 'onChange',
  });
  
  const formFields: InputProps[] = [
    {
      name: "matrcula",
      label: "Matrcula",
      component: <IonInput type="text" />,
    },
    {
      name: "nombreDoc",
      label: "Nombre",
      component: <IonInput type="text" />,
    },
    {
      name: "apellidoDoc",
      label: "Apellido",
      component: <IonInput type="text" />,
    },
    {
      name: "contrasea",
      label: "Contrasea",
      component: <IonInput type="password" clearOnEdit={false} />,
    },
    {
      name: "trminos",
      label: "Acepto los trminos y condiciones",
      component: <IonCheckbox slot="start" />,
    },
  ];
  
    const registerUser = (data: any) => {
      console.log("Creando nueva cuenta con: " + JSON.stringify(data,null,4));
      window.alert("Creando nueva cuenta con: " + JSON.stringify(data,null,4));
      return false;
    };  
  
    return (
    <IonPage>
      <IonHeader> 
        <CommonHeader /> 
      </IonHeader>
      
      <IonContent fullscreen id="Tab1Content">
        <div className="ion-padding">
          <IonText color="muted">
            <h2>Datos del Doctor</h2>
          </IonText>

          <form onSubmit={handleSubmit(registerUser)}>
            {formFields.map((field, index) => (
              <Input {...field} control={control} key={index} errors={errors} />
            ))}
  
            {/*<IonButton
              expand="block"
              type="button"
              className="ion-margin-top"
              onClick={() => {
              reset();              
              }}>
              Resetear
            </IonButton>*/}
  
            <IonButton expand="block" type="submit" className="ion-margin-top">
              Registrarse
            </IonButton>
          </form>
        </div>
      </IonContent>
      
      <IonFooter> 
        <CommonFooter /> 
      </IonFooter>
    </IonPage>
  );
};

export default Tab1;