import {
  IonContent,
  IonPage,
  IonText,
  IonInput,
  IonButton,
  IonCheckbox,
  IonHeader,
  IonToolbar,
  IonTitle,
  IonFooter,
  IonTabButton,
} from "@ionic/react";
import React from "react";
import { FieldValues, useForm } from "react-hook-form";
import Input, { InputProps } from "../components/Input";
import * as yup from "yup";
import { yupResolver } from '@hookform/resolvers/yup';
import CommonHeader from '../components/CommonHeader';
import CommonFooter from '../components/CommonFooter';

const Tab2: React.FC = () => {
  const validationSchema = yup.object().shape({
    documento: yup.number().required("informacin requerida").min(1000000).max(100000000),
    nombrePac: yup.string().required("informacin requerida").min(2).max(32),
    apellidoPac: yup.string().required("informacin requerida").min(2).max(32),
    credencial: yup.string().required("informacin requerida").min(4).max(32),
    genrico: yup.string().required("informacin requerida").min(4).max(32),
    producto: yup.string().required("informacin requerida").min(4).max(32),
    presentacin: yup.string().required("informacin requerida").min(4).max(32),
    cantidad: yup.number().required("informacin requerida").min(1).max(3),
  });
  let initialValues = {
    documento: undefined,
    nombrePac: undefined,
    apellidoPac: undefined,
    credencial: undefined,
    genrico: undefined,
    producto: undefined,
    presentacin: undefined,
    cantidad: undefined,
  };
  const { control, handleSubmit, reset, formState: { errors } } = useForm<FieldValues>({
    defaultValues: { ...initialValues },
    resolver: yupResolver(validationSchema),
    mode: 'onSubmit',
    reValidateMode: 'onChange',
  });
  
  const formFieldsP: InputProps[] = [
    {
      name: "documento",
      label: "Documento",
      component: <IonInput type="number" />,
    },
    {
      name: "nombrePac",
      label: "Nombre",
      component: <IonInput type="text" />,
    },
    {
      name: "apellidoPac",
      label: "Apellido",
      component: <IonInput type="text" />,
    },
    {
      name: "credencial",
      label: "Credencial",
      component: <IonInput type="text" />,
    },
  ];

  const formFieldsM: InputProps[] = [
    {
      name: "genrico",
      label: "Genrico",
      component: <IonInput type="text" />,
    },
    {
      name: "producto",
      label: "Producto",
      component: <IonInput type="text" />,
    },
    {
      name: "presentacin",
      label: "Presentacin",
      component: <IonInput type="text" />,
    },
    {
      name: "cantidad",
      label: "Cantidad",
      component: <IonInput type="number" />,
    },
  ];

  const generatePrescription = (data: any) => {
    console.log("Creando nueva receta con: " + JSON.stringify(data,null,4));
    window.alert("Creando nueva receta con: " + JSON.stringify(data,null,4));
    return false;
  };

  return (
    <IonPage>
      <IonHeader> 
        <CommonHeader /> 
      </IonHeader>

      <IonContent fullscreen id="Tab2Content">
        <div className="ion-padding">
          <IonText color="muted">
          <h2>Datos de la receta</h2>
          </IonText>

          <form onSubmit={handleSubmit(generatePrescription)}>
            <IonText>PACIENTE</IonText>
            <IonToolbar>
              {formFieldsP.map((field, index) => (
                <Input {...field} control={control} key={index} errors={errors} />
              ))}
            </IonToolbar>
            <br/>
            
            <IonText>MEDICAMENTO</IonText>
            <IonToolbar>
              {formFieldsM.map((field, index) => (
                <Input {...field} control={control} key={index} errors={errors} />
              ))}
            </IonToolbar>
  
            {/*<IonButton
              expand="block"
              type="button"
              className="ion-margin-top"
              onClick={() => {
              reset();              
              }}>
              Resetear
            </IonButton>*/}

            <IonButton expand="block" type="submit" className="ion-margin-top">
              Recetar
            </IonButton>
          </form>
        </div>
      </IonContent>
      
      <IonFooter> 
        <CommonFooter /> 
      </IonFooter>
    </IonPage>
  );
};

export default Tab2;