import { FC } from "react";
import { IonItem, IonLabel, IonText, IonCheckbox } from "@ionic/react";
import {
  Controller,
  Control,
} from "react-hook-form";

export interface InputProps {
  label?: string;
  name: string;
  control?: Control;
  component: JSX.Element;
  errors?: 	any;
  //errors?: NestDataObject<Record<string, any>, FieldError>;
}

const Input: FC<InputProps> = ({ label, name, control, component, errors }) => {
  return (
    <>
      <IonItem>
        {label && <IonLabel position = {component.type != IonCheckbox ? "floating" : undefined }>{label}</IonLabel>}
        <Controller
          name={name}            
          control={control}
          render={({
            field: { onChange, onBlur, value, ref },
            }) =>
            ( <> { component.type != IonCheckbox ?
              <component.type {...component.props} value={value} onIonChange = {onChange} /> :
              <IonCheckbox {...component.props} checked={value} onIonChange={({ detail: { checked } }) => onChange(checked)} /> // checkbox bug
            } </> ) 
          }
        />
      </IonItem>
      
      {errors && errors[name] && (
        <IonText color="danger" className="ion-padding-start">
          <small>
            <span role="alert" id={`${name}Error`}>
              {errors[name].message} 
            </span>
          </small>
        </IonText>
      )}
     </> 
  );
};

export default Input;